import { Injectable } from '@angular/core';
import { GraphData } from '../data/graph-data';
import { MockHttpDataService } from './mock-http-data-service';

@Injectable()
export class GraphDateServiceService {

  constructor(private mockHttpDataService: MockHttpDataService) { }

  getLatestGraphData(deviceName: string): Promise<GraphData> {
    console.log(`Read Get latest graph data for device ${deviceName}`);
    return this.mockHttpDataService.getLatestData(deviceName);
  }

}
