import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import { GraphDateServiceService } from './services/graph-date-service.service';
import { GraphData } from './data/graph-data';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {

  title = 'MIOT Demo Screen';
  barChartLabels: string[] = [];
  barChartData: any[] = [
    {
      barPercentage: 1,
      barThickness: 6,
      maxBarThickness: 8,
      minBarLength: 0,
      data: [],
      label: 'Battery status'
    }
  ];

  pieChartData: number[] = [0, 0];
  invalidDevice = false;
  invalidDeviceErrorMsg = '';
  timerEfficiencyVal = 0;

  constructor(public graphDateServiceService: GraphDateServiceService, public location: Location) { }

  ngOnInit(): void {
    this.getLatestGraphData();
  }

  getLatestData(event: Event) {
    console.log(`Get latest data clicked`);
    this.getLatestGraphData();
  }

  private getLatestGraphData() {
    const uriTokens = this.location.path().split('?');
    const queryParmString = uriTokens[uriTokens.length - 1];
    const queryParams = queryParmString.split('&&');
    const deviceName = queryParams[0].split('=')[1];
    console.log(queryParams);
    if (!deviceName) {
      this.setErrorMessage('', 'Unable to read data for empty device name. Url path should be of pattern http://<IP>:<PORT>/<DEVICE_NAME>');
      return;
    }
    this.resetErrorFlags();
    this.graphDateServiceService.getLatestGraphData(deviceName).then((graphData: GraphData) => {
      const batterySave = [];
      const barChartXAxisLables = [];
      this.barChartData[0].data = [];
      const prodcutiveWindowTimeSet: Set<string> = new Set<string>();
      for (const productiveWindowTimeFrame of graphData.metrics['productive-windows']) {
        const productiveTime = new Date(productiveWindowTimeFrame * 1000);
        prodcutiveWindowTimeSet.add(
          `${productiveTime.getHours()}:${productiveTime.getMinutes() >= 10 ? productiveTime.getMinutes() : '0' + productiveTime.getMinutes()}:${productiveTime.getSeconds() >= 10 ? productiveTime.getSeconds() : '0' + productiveTime.getSeconds()} `
        );
      }

      for (const activeWindowTimeFrameMS of graphData.metrics['active-windows']) {
        // console.log(`Looping for active window ${activeWindowTimeFrameMS} `);
        const activeWindowTime = new Date(activeWindowTimeFrameMS * 1000);
        const activeWindowTimeFrame =
          `${activeWindowTime.getHours()}:${activeWindowTime.getMinutes() >= 10 ? activeWindowTime.getMinutes() : '0' + activeWindowTime.getMinutes()}:${activeWindowTime.getSeconds() >= 10 ? activeWindowTime.getSeconds() : '0' + activeWindowTime.getSeconds()} `;
        if (prodcutiveWindowTimeSet.has(activeWindowTimeFrame)) {
          batterySave.push(2);
        } else {
          batterySave.push(1);
        }
        barChartXAxisLables.push(activeWindowTimeFrame);
      }
      batterySave.push(0);
      barChartXAxisLables.push('');
      // const cloneBarChartData = { ...this.barChartData };
      const cloneBarChartData = JSON.parse(JSON.stringify(this.barChartData));
      cloneBarChartData[0].data = batterySave;
      this.barChartData = cloneBarChartData;
      this.barChartLabels = barChartXAxisLables;
      this.timerEfficiencyVal = graphData.metrics['timer-efficiency'];
      this.pieChartData = [
        graphData.metrics['productive-active-windows-per-day'],
        graphData.metrics['active-windows-per-day'] - graphData.metrics['productive-active-windows-per-day']
      ];

    }).catch((error) => {
      console.error('Could not able to get latest graph data');
      console.error(error);
      this.setErrorMessage(deviceName);
    });
  }

  private setErrorMessage(deviceName: string, errorMsg: string = `Unable to read data for device with id ${deviceName} `) {
    this.invalidDevice = true;
    this.invalidDeviceErrorMsg = errorMsg;
  }

  private resetErrorFlags() {
    this.invalidDevice = false;
    this.invalidDeviceErrorMsg = '';
  }
}
